.App{display:flex;flex-direction:column;align-items:center;justify-content:start;height:100vh;width:100%;margin:0;overflow-y:scroll}.App>header{display:flex;flex-direction:column;justify-content:center;align-items:center;position:fixed;top:0;width:100%;background:#fff;padding-top:48px}.App>header>h1{margin:0;font-size:2rem;font-weight:700}.App>.footer-container{z-index:1000;display:flex;flex-direction:column;justify-content:center;align-items:center;position:fixed;padding-bottom:24px;bottom:0;width:100%;background:#fff;box-shadow:0 -12px 12px 12px #fff}.App>.footer-container>span{margin-bottom:2px;width:100%;text-align:center;font-size:.8rem;color:#999}