* {
  box-sizing: border-box;
  font-family: 'Outfit', 'Noto Sans TC', sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -moz-transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -ms-transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition: 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

html {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: 'Outfit', 'Noto Sans TC', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
